/* Profile */
.profile-row {
  margin-top: 5em;
}

.profile-about {
  /* background-color: rgb(26, 26, 26); */
  background-color: rgba(26, 26, 26, 0.25);
  padding: 1.3em;
  font-size: 1.1em;
  margin-top: 10em;
}

.profile-heading-name {
  font-size: 2.5em;
}

.profile-info {
  margin-top: 8em;
}

/* Work row */
.work-row {
  margin-top: 10em;
}

.work-item {
  background-color: rgb(20, 20, 20);
  padding: 1.5em;
}

.project-item {
  background-color: rgb(20, 20, 20);
  /* box-shadow: 0px 0px 20px #262626; */
  /* min-height: 15em; */
  transition: box-shadow 0.5s;
}

.project-item:hover {
  box-shadow: 0px 0px 20px #262626;

}

.project-item-info {
  padding: 1.5em;
}

.project-item-info-bottom {
  position: absolute;
  font-size: 0.85em;
  bottom: 0;
}

.work-item-row {
  margin-top: 6em;
}

/* Skills row */
.skills-row {
  margin-top: 10em;
  min-height: 30em;
}

.skill-list {
  margin-top: 5em;
}

.skill-item {
  /* background-color: rgb(68, 38, 36); */
  background-color: rgb(30, 30, 30);
  padding: 0.4em;
  margin-top: 1em;
  text-align: center;
  font-size: 1em;
}

/* Footer */
.footer {
  min-height: 20em;
  background-color: rgb(20, 20, 20);
  padding: 2em;
  padding-bottom: 0;
  margin-top: 10em;
}

.footer-title {
  font-size: 2.1em;
}

.footer-title-underline {
  border: 1px solid rgb(68, 68, 68);
}

.contact-link {
  margin-bottom: 0.3em;
  color: #868686;
}

.footer-copyright {
  font-size: 0.6em;
}

.footer-copyright a {
  color: inherit;
  text-decoration: none;
}

.social-links-list {
  margin-top: 0.5em;
  list-style: none;
  padding-left: 0.8em;
}

.social-links-list a {
  color: grey;
  text-decoration: none;
}

/* Reusable classes */
.text-grey {
  color: grey;
}

.text-date {
  color: #bbb;
}

.navbar-main-1 {
  /* background-color: #6a6a6a1c !important; */
  background-color: #09090942 !important;
  height: 4.5em;
  left: 0;
  top: 0;
  width: 100%;
  position: absolute;
}

.navbar-main-1 .navbar-brand {
  font-size: 1.3em !important;
}


/* Slide in animations */

.box-to-slide-in-1 {
  opacity: 0;
}

.box-to-slide-in-1-left {
  opacity: 0;
}

/* CSS3 Slide in */
.slide-in-1 {
  transform: translateY(250px);
  transform: translateX(250px);
  animation: slide-in-1 1.5s ease forwards;
}

.slide-in-1-delay {
  transform: translateY(250px);
  transform: translateX(250px);
  animation: slide-in-1 1.5s ease forwards;
  animation-delay: 0.65s;
}

.slide-in-1-left {
  transform: translateY(250px);
  transform: translateX(-250px);
  animation: slide-in-1 1.5s ease forwards;
}

.fade-in-1 {
  animation: fade-in-1 1s ease forwards;
}

@keyframes slide-in-1 {
  to {
    transform: translateY(0);
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fade-in-1 {
  to {
    opacity: 1;
  }
}

/* A dynamic overview */
.overview-dynamic {
  position: fixed;
  border-left: 0.2em solid rgb(85, 85, 85);
  height: 13em;
  width: 12em;
  left: 3em;
  top: 17em;
  /* z-index: -1; */
  font-size: 1.2em;
}

.follow-view-y {
  position: relative;
  top: 3em;
  list-style: none;
  padding-left: 0.8em;
}

/* customize bootstrap list group classes */

.list-group-1 {
  /* background-color: transparent; */
}

.list-group-1 a {
  background-color: transparent;
  color: grey;
  padding: 0.1em;
  border: none;
  transition: color 0.5s, text-shadow 0.5s;
}

.list-group-1 a:hover {
  background-color: transparent;
  color: white;
  text-shadow: 0px 0px 6px white;
}

.list-group-1 .active {
  background-color: transparent;
  color: white;
  border: none;
  text-shadow: 0px 0px 6px white;
}


